.main-nav {
  position: relative;

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.main-nav__bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-nav--opened .main-nav__bar {
  background-color: @color-blue;

  @media (min-width: @desktop-width) {
    background-color: unset;
  }
}

.main-nav__logo {
  width: 74px;
  height: 23px;

  cursor: pointer;

  &:hover svg {
    fill: @color-pink;
  }

  &:active svg {
    fill: fade(@color-white, 30%);
  }
}

.main-nav__toggle {
  .button();
  width: 50px;
  height: 24px;

  &:hover svg {
    fill: @color-pink;
  }

  &:active svg {
    fill: fade(@color-white, 30%);
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  width: 23px;
  height: 23px;

  @media (max-width: (@mobile-width-only)) {
    margin-right: 3px;
  }
}

.main-nav--opened .main-nav__toggle-icon--open {
  display: none;
}

.main-nav--closed .main-nav__toggle-icon--close {
  display: none;
}

.main-nav__menu {
  position: absolute;
  width: 100%;
  background-color: @color-blue;
  border-top: 1px solid @color-black;

  @media (min-width: @desktop-width) {
    position: static;
    max-width: 620px;
    background-color: unset;
    border: none;
  }
}

.main-nav--closed .main-nav__menu {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
  }
}

.main-nav__list {
  .list();

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.main-nav__item {
  border-top: 1px solid fade(@color-white, 15%);
  border-bottom: 1px solid @color-black;

  @media (min-width: @desktop-width) {
    border: none;
    margin-right: 78px;

    &:last-child {
      margin-right: 0;
    }

    &--current {
      box-shadow: 0 2px 0 @color-white;
    }
  }
}

.main-nav__link {
  .button();
  padding: 14.5px 0;

  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;

  @media (min-width: @desktop-width) {
    font-weight: 400;
    line-height: 18px;
    text-transform: capitalize;
  }

  &:hover {
    color: @color-pink;
  }

  &:active {
    color: fade(@color-white, 30%);
  }
}
